<template>
  <div class="channelcontent" v-if="channeldetails.tagInfo">
    <div class="back" @click="$router.go(-1)">
      <van-icon name="arrow-left" />
    </div>
    <van-sticky>
      <div id="top">
        <h1>{{channeldetails.tagInfo.name}}</h1>
      </div>
    </van-sticky>

    <div
      class="channel_header"
      :style="{backgroundImage:'url('+channeldetails.tagInfo.headerImage+')'}"
    >
      <div class="title">
        <h1>{{channeldetails.tagInfo.name}}</h1>
        <p>{{channeldetails.tagInfo.description}}</p>
        <p>
          <span>{{channeldetails.tagInfo.tagFollowCount}}人关注 |</span>
          <span> {{channeldetails.tagInfo.lookCount}}人参与</span>
        </p>
        <van-button round type="info" size="small">+关注</van-button>
      </div>
      <div class="mask"></div>
    </div>

    <div class="lump" v-for="(item, index) in videomessage" :key="index">
      <template v-if="item.type=='textCard'">
        <KaiyanTitle :item="item"></KaiyanTitle>
      </template>

      <template v-if="item.type=='followCard'">
        <ul>
          <VideoList :item="item"></VideoList>
        </ul>
      </template>

      <template v-if="item.type=='videoSmallCard'">
        <ul>
          <VideoSmallCard :item="item"></VideoSmallCard>
        </ul>
      </template>

      <template v-if="item.type=='autoPlayFollowCard'">
        <ul>
          <VideoItem :item="item"></VideoItem>
        </ul>
      </template>
    </div>

    <!-- <KaiyanTitle>近期热门</KaiyanTitle>
    <ul>
      <VideoList v-for="(item, index) in videomessage" :key="index" :item="item"></VideoList>
    </ul>
    <ul>
      <VideoSmallCard v-for="(item, index) in videomessage" :key="index" :item="item"></VideoSmallCard>
    </ul>
    <KaiyanTitle>最近更新</KaiyanTitle>
    <ul>
      <VideoItem v-for="(item, index) in videomessage" :key="index" :item="item"></VideoItem>
    </ul>-->
  </div>
</template>

<script>
import KaiyanTitle from "@/components/KaiyanTitle.vue";
import VideoItem from "@/components/VideoItem.vue";
import VideoList from "@/components/VideoList.vue";
import VideoSmallCard from "@/components/VideoSmallCard.vue";

export default {
  components: {
    KaiyanTitle,
    VideoItem,
    VideoList,
    VideoSmallCard
  },
  data() {
    return {
      channeldetails: {},
      videomessage: []
    };
  },
  methods: {
    getChannelDetails(tagId) {
      this.axios
        .get(
          "http://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v6/tag/index?id=1023&udid=917e512b517d461cbf22eaf4dbf759ee7e15dc29&vc=580&vn=6.1&size=1080X1920&deviceModel=MI%20MAX%202&first_channel=eyepetizer_xiaomi_market&last_channel=eyepetizer_xiaomi_market&system_version_code=25",
          {
            params: {
              id: tagId
            }
          }
        )
        .then(response => {
          this.channeldetails = response.data;
          // window.console.log(response.data);
        });
    },
    getVideoItem(tagId) {
      let videomessage = JSON.parse(
        window.localStorage.getItem("videomessage")
      );
      if (videomessage && Date.now() < videomessage.time) {
        this.videomessage = videomessage.data;
      }
      this.axios
        .get(
          "http://api.kele8.cn/agent/http://baobab.kaiyanapp.com/api/v1/tag/videos?udid=917e512b517d461cbf22eaf4dbf759ee7e15dc29&vc=580&vn=6.1&size=1080X1920&deviceModel=MI%20MAX%202&first_channel=eyepetizer_xiaomi_market&last_channel=eyepetizer_xiaomi_market&system_version_code=25",
          {
            params: {
              id: tagId
            }
          }
        )
        .then(response => {
          this.videomessage = response.data.itemList;
          // window.console.log(response.data);
          window.localStorage.setItem(
            "videomessage",
            JSON.stringify({
              time: Date.now() + 30 * 60 * 1000,
              data: this.videomessage
            })
          );
        });
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      vm.getChannelDetails(to.query.tagId);
      vm.getVideoItem(to.query.tagId);
    });
  }
};
</script>

